* {
    padding: 0;
    margin: 0;
}

.appWrapper {
    display: grid;
    grid-template-areas: "h h h h" ". n c .";
    grid-template-rows: 80px auto;
    grid-template-columns: 1fr 0.5fr 3fr 1fr;
    grid-gap: 0.5em;
}


.head {
    grid-area: h;
    background-color: black;
    z-index: 1;
    /* border-radius: 0 0 10px 10px;
    box-shadow: 0 10px 3px;
    position: sticky;
    top: 0;*/

}

.nav {
    grid-area: n;
    height: 20em;
    background-color: #61dafb;
    border-radius: 10px;
    z-index: 1;
    /*box-shadow: 0 0 3px 1px;
    position: sticky;
    top: 75px;
    */
}

.appWrapperContent {
    grid-area: c;
    background-color: ghostwhite;
    border-radius: 10px;
    /*box-shadow: 0 0 3px 1px;*/
    z-index: 1;
}

.side {
    width: 100%;
    height: 100%;
    color: white;
    position: absolute;
    z-index: 0;
}

.preloader {
    margin-left: 20px
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active  {
    -webkit-box-shadow: 0 0 0 30px white inset !important;
}

@media screen and (max-width: 600px){
    .appWrapper {
        display: grid;
        grid-template-areas: "h h" "n c";
        grid-template-rows: 80px auto;
        grid-template-columns: 0.5fr 3fr;
        grid-gap: 0.5em;
    }
}


